import {createStore} from 'vuex'


const store = createStore({
    state: {
        ins1: 1,
        ins2: 1,
        ins3: 1,
        insf: 1
    },
    mutations: {
      CHEAD(state, value){
        state.ins1 = value[0]
        state.ins2 = value[1]
        state.ins3 = value[2]
        state.insf = value[3]
    }
    },
    actions: {

    },
    modules: {

    }
})
export default store