<script>
import {onMounted, ref, watchEffect} from "vue";
import {useStore} from 'vuex'
export default {
  name: "pic_set",
  setup() {
    const store = useStore()
    onMounted(()=>{store.commit('CHEAD', [0,0,1,1])})
    let arr1 = []
    let arr2 = []
    let arr3 = []
    let arr4 = []
    let arr5 = []
    let arr6 = []
    let arr7 = []
    let arr8 = []
    let ins2 = ref(0)
    let ins3 = ref(0)
    let ins4 = ref(0)
    let ins5 = ref(0)
    let ins6 = ref(0)
    let ins7 = ref(0)
    let ins8 = ref(0)
    for (let i = 351; i <= 385; i++) {
      arr8.push(i)
    }
    for (let i = 301; i <= 350; i++) {
      arr7.push(i)
    }
    for (let i = 251; i <= 300; i++) {
      arr6.push(i)
    }
    for (let i = 201; i <= 250; i++) {
      arr5.push(i)
    }
    for (let i = 151; i <= 200; i++) {
      arr4.push(i)
    }
    for (let i = 101; i <= 150; i++) {
      arr3.push(i)
    }
    for (let i = 50; i <= 100; i++) {
      arr2.push(i)
    }
    for (let i = 0; i <= 49; i++) {
      arr1.push(i)
    }
    let scrolltop = ref(window.scrollY || document.documentElement.scrollTop || document.body.scrollTop)

    function sev() {
      scrolltop.value = window.scrollY || document.documentElement.scrollTop || document.body.scrollTop
      console.log(scrolltop.value, ins2.value)
    }

    watchEffect(() => {
      if (scrolltop.value > 6000) {
        console.log(1111111111111111111)
        ins2.value = 1
      }
      if (scrolltop.value > 12000) {
        console.log(1111111111111111111)
        ins3.value = 1
      }
      if (scrolltop.value > 18000) {
        console.log(1111111111111111111)
        ins4.value = 1
      }
      if (scrolltop.value > 24000) {
        console.log(1111111111111111111)
        ins5.value = 1
      }
      if (scrolltop.value > 30000) {
        console.log(1111111111111111111)
        ins6.value = 1
      }
      if (scrolltop.value > 35000) {
        ins7.value = 1
      }
      if (scrolltop.value > 40000) {
        ins8.value = 1
      }
    })
    onMounted(() => {
      window.addEventListener('scroll', sev)
    })
    return {
      arr2, arr1, arr3, arr4, arr5, arr6, arr7, arr8,
      ins2, ins3, ins4, ins5, ins6, ins7, ins8, scrolltop
    }
  }
}
</script>

<template>
  <div class="container">
    <div><img v-for="i of arr1" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins2"><img v-for="i of arr2" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins3"><img v-for="i of arr3" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins4"><img v-for="i of arr4" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins5"><img v-for="i of arr5" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins6"><img v-for="i of arr6" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins7"><img v-for="i of arr7" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
    <div v-if="ins8"><img v-for="i of arr8" :src="require('@/assets/girls/'+i+'.jpg')" alt="" :key="i"></div>
  </div>
</template>

<style scoped>
.container {
  width: 1000px;
  margin: 0 auto;
  column-count: 3;
  display: block;
}
div{
  display: inline;
}
img {
  width: 100%
}

img:hover {
  box-shadow: 0 0 20px pink;
  transform: scale(1.1);
  transition-duration: 0.3s;
}
</style>