<script>
import {ref, inject, reactive, onUpdated, onMounted} from 'vue'
import {useRoute} from 'vue-router'
import {useStore} from 'vuex'
// import {getCurrentInstance} from 'vue';


export default {
  name: 'jojo_charactor',
  setup() {
    const store = useStore()
    onMounted(()=>{store.commit('CHEAD', [1,1,1,1])})
    let route = useRoute()
    let currentpage = parseInt(route.path.slice(17))
    let chinfo = reactive(inject('chinfo').characters)
    let maxpage = Math.floor(chinfo.length / 4) + 1
    let stdinfo = inject('stdinfo').stands
    chinfo = chinfo.slice(4 * (currentpage - 1), 4 * currentpage)
    let len9 = []
    for (let i = 1; i <= 9; i++) {
      len9.push(i)
    }
    len9 = reactive(len9)
    if (currentpage <= 4) {
      len9 = [1, 2, 3, 4, 5, 6, 7, 8]
    } else if (currentpage > 4 && currentpage < maxpage - 4) {
      len9 = [currentpage - 3, currentpage - 2, currentpage - 1, currentpage, currentpage + 1, currentpage + 2, currentpage + 3]
    } else {
      len9 = [maxpage - 6, maxpage - 5, maxpage - 4, maxpage - 3, maxpage - 2, maxpage - 1, maxpage]
    }
    currentpage = ref(currentpage)
    maxpage = ref(maxpage)
    onUpdated(() => {
      window.location.reload();
    })
    return {
      // chinfo,
      len9,
      currentpage,
      maxpage,
      chinfo,
      stdinfo
    }
  }
}
</script>

<template>
  <div class="breadline">
    <div class="nav ib">
      <router-link to="/index">首页</router-link>
      >
      <router-link to="/characters/list/1">人物概览</router-link>
    </div>
    <div class="aside">
      <div class="aside-head">人&nbsp;&nbsp;气&nbsp;&nbsp;排&nbsp;&nbsp;行</div>
      <router-link to="/characters/detail/63"><div class="rank cf">
        <div class="mugshot fl mugshot1"></div>
        <div class="name fr">吉良吉影</div>
      </div></router-link>
      <router-link to="/characters/detail/28"><div class="rank">
        <div class="mugshot fl mugshot2"></div>
        <div class="name fr">空条承太郎</div>
      </div></router-link>
      <router-link to="/characters/detail/8"><div class="rank">
        <div class="mugshot fl mugshot3"></div>
        <div class="name fr">迪奥</div>
      </div></router-link>
    </div>
  </div>

  <div class="container cf">

    <div class="order fl cf">
      <div class="ib order-btn">全部</div>
      <div class="ib order-btn">核心⭐</div>
      <div class="count fr">收录共{{}}人</div>
    </div>

    <div v-for="ch of chinfo" class="content fl cf" :key="ch.id">

      <div class="pic fl">
        <img class="picself" alt="" :src="require('@/assets/mypic/'+ch.image)">
      </div>
      <div class="chinfo_name fl chinfo">
        <span style="font-size: 15px">你叫啥：</span>{{ ch.name }}
      </div>
      <router-link :to="'/characters/detail/'+ch.id" :id="ch.id">
        <div class="lookinto fr">
           <span class="span1">让我看看</span>
          </div>
      </router-link>
      <div class="chinfo_side fl chinfo">
        <span style="font-size: 15px">简&nbsp;介：</span>{{ ch.side }}
      </div>


      <div class="chinfo_stand fl chinfo">
        <span style="font-size: 15px">替身信息：</span>{{ stdinfo[ch.stand - 1].name }}
      </div>
      <div class="fav fr">
        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;赞
      </div>
    </div>

    <div class="turnpage fl">

      <div v-if="currentpage!==1" @click="currentpage-=1" class="updown up">
        <router-link :to="'/characters/list/'+`${currentpage-1}`">上一页</router-link>
      </div>

      <div v-else class="updown hide"></div>

      <router-link to="/characters/list/1">
        <div v-if="currentpage>4" @click="currentpage=1" class="pages fl">...</div>
      </router-link>

      <div v-for="i of len9" @click="currentpage=i" class="pages fl" :key="i">
        <router-link :to="'/characters/list/'+i">{{ i }}</router-link>
      </div>

      <router-link :to="'/characters/list/'+maxpage">
        <div v-if="currentpage<maxpage-4" @click="currentpage=maxpage;console.log(currentpage)" class="pages fl">...
        </div>
      </router-link>

      <div v-show="currentpage!==maxpage" @click="currentpage+=1" class="updown down">
        <router-link :to="'/characters/list/'+`${currentpage+1}`">下一页</router-link>
      </div>

    </div>

  </div>


</template>

