<script>


import {ref, inject, reactive} from "vue";
import {useRoute} from "vue-router";
export default {
  name: "chdetail_pages",
  setup() {
    let route = useRoute()
    let id = ref(parseInt(route.path.slice(19))-1)
    let chinfo = reactive(inject('chinfo').characters[id.value])

    return {
      id,
      chinfo
    }
  }
}
</script>

<template>
  <div class="container cf">

    <div class="breadline">
      <div class="nav ib">
        <router-link to="/index">首页</router-link>
        >
        <router-link to="/characters/list/1">人物概览</router-link>
        >
        <router-link :to="'/characters/detail/'+id">人物详情</router-link>

      </div>
    </div>

    <div class="content fl cf">
      <div class="c-img fl">
        <img class="c-imgself" alt="" :src="require('@/assets/mypic/'+chinfo.image)">
      </div>
      <div class="c-info fr">
        <ul class="c-info-ul">
          <li class="cinfo-li">姓名: {{chinfo.name}}</li>
          <li class="cinfo-li">状态: {{chinfo.status}}</li>
          <li class="cinfo-li">情况: {{chinfo.side}}</li>
        </ul>
      </div>
      <div class="c-detail fl">
        {{chinfo.detail }}
      </div>
    </div>


    <div class="aside">
      <div class="aside-head">当&nbsp;&nbsp;季&nbsp;&nbsp;信&nbsp;&nbsp;息</div>
      <div class="aside-body">
        <img class="cover" alt="" :src="require('@/assets/mypic/seasons/'+ chinfo.season_id+'.png')">
        <div class="seasonsmini">第{{chinfo.season_id}}季</div>
      </div>
    </div>


  </div>
</template>

<style scoped>
